.icon-left {
  position: absolute;
  top: 10px; 
  left: 10px; 
}
.icon-right {
  position: absolute;
  bottom: 10px;
  right: 10px;
}
.season-display {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
}
.winter {
  background-color: aliceblue;
}
.summer {
  background-color: floralwhite;
}
.summer i, .summer h1 {
  color: orange;
}
.winter i, .winter h1 {
  color: lightskyblue;
}